* {
    border: 0;
    padding: 0;
    margin: 0;
    list-style: none;
    box-sizing: border-box;
}
html,
body,
#root,
.App {
    width: 100%;
    height: 100%;
    background: #f9f9fb;
}
%juzhong {
    display: flex;
    justify-content: center;
    align-items: center;
}
%whhundred {
    width: 100%;
    height: 100%;
}
%nochooseword {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
%scrollbar {
    &::-webkit-scrollbar {
        /*滚动条整体样式*/
        width: 4px; /*高宽分别对应横竖滚动条的尺寸*/
        height: 1px;
    }
    &::-webkit-scrollbar-thumb {
        /*滚动条里面小方块*/
        border-radius: 10px;
        box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
        background: #4C4C4C;
    }
    &::-webkit-scrollbar-track {
        /*滚动条里面轨道*/
        box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
        border-radius: 10px;
        background: #fff;
    }
}
